  var db = firebase.database();


class Details extends React.Component {
	state={
		
	}
	constructor(props) {
		super(props);
		this.handleChange=this.handleChange.bind(this);
		this.componentDidMount = this.componentDidMount.bind(this);
	}
	goToURL(object,key){
		var url = ''
		for(let i=0;i<object.length;i++){
			if(object[i].shortURL==key){
				url = object[i].longURL;
			}
		}
		setTimeout(() => {
			window.location.href = url;
		}, 2000);
	}
	componentDidMount(props){
		var dataObject = [];
		var wholeData = {};
		var readData = db.ref('links');
		var self = this;
		var hashKey = window.location.hash.substring(1,(window.location.hash).length);
		readData.on('value', function(snapshot) {
			wholeData = snapshot.val();
			Object.keys(wholeData).forEach(key=>{
				var newObj = {};
				newObj.shortURL = wholeData[key].shortURL;
				newObj.longURL = wholeData[key].longURL;
				dataObject.push(newObj);
			})
			self.goToURL(dataObject,hashKey)
		});
	}
	handleChange = (event) => {
		const target = event.target;
		const name = target.name;
		this.setState({
		  [name]: target.value
		})
	}  
	
	render() {
		return (
			<div>
			<div className="mobile-thanks">
				<h3 className="heading-text thanks-heading">Thank you for using MakeURL</h3>
			</div>
			
				<div className="login">
					<h4 className="please-wait">Please wait while your page is being redirected....</h4>
					<img className="loaderImage" src={loader}></img>
				</div>
			</div>
		);
	}
}

export default withRouter(Details);